@import '../SiteColorPalette.scss';

.DropDownMenuContainerOpen{
    position: absolute;
    z-index: 999;
    right: 0;
    top: 0;
    width: 350px;
    height: 100vh;
    background: $_dark_slate;
    align-items: center;
    display: flex;
    flex-direction: column;
    transition: 1s ease-in-out;
    
    .ExitIconContainer{
        width: 100%;
        text-align: right;
        background-color: $_dark_slate_lighter;

        .ExitIcon{
            color: $_red;
            margin-top: 1.2rem;
            margin-right: 1.5rem;
            margin-bottom: 1rem;
            font-size: 40px;
            cursor: pointer;

            &:hover{
                color: $_red_darker;
            }
        }
    }

    .DropDownMenuTitles{
        display: flex;
        flex-direction: column;
        width: 100%;
        margin-top: 1rem;
        margin-left: 2rem;

        .DropDownMenuRowContainer{
            display: flex;
            flex-direction: row;
            color: white;
            align-items: center;
            font-size: 30px;
            padding: 1rem;
            border-radius: 6px;

            .DropDownMenuLinks{
                display: flex;
                margin: 10px;
                align-items: center;
                cursor: pointer;
                text-decoration: none;
                color: white;
                width: 100%;
                height: 100%;
                font-size: 1.5rem;
            }   

            &:hover{
                background-color: $_dark_slate_darker;
            }
        }

        button{
            width: fit-content;
            font-size: $_large;
            font-weight: bold;
            color: white;
            background-color: $_red;
            padding: 20px;
            margin-top: 1rem;
            text-decoration: none;
            border: 1px;
            outline-style: none;
            border-radius: 10px;
            cursor: pointer;

            &:hover{
                background-color: $_red_darker;
            }
        }
    }

    .ContactContainer{
        display: flex;
        justify-content: center;
        color: $_white_darker;
        margin: 2rem;
        font-size: 40px;

        .ContactIcon{
            text-decoration: none;
            text-decoration-color: $_white_darker;
            color: $_white_darker; 
            margin: 10px;
        }
    }

}

.DropDownMenuContainerClosed{
    @extend .DropDownMenuContainerOpen;

    position: absolute;
    right: -50%;
}